/* BREAKPOINT SETTINGS */
/* BREAKPOINT MIXINS */
.Product {
  display: flex;
  width: 100%;
  border-radius: 8px;
  overflow: hidden; }
  @media (max-width: 600px) {
    .Product {
      flex-direction: column; } }
  .Product h2 {
    margin: 0 0 19px 0; }
    @media (max-width: 900px) {
      .Product h2 {
        margin: 0 0 1.1vw 0; } }
    @media (max-width: 600px) {
      .Product h2 {
        margin: 0 0 9px 0; } }
  .Product .image {
    width: 40.625%; }
    @media (max-width: 600px) {
      .Product .image {
        width: 100%; } }
    .Product .image img {
      width: 100%;
      height: auto;
      display: block; }
  .Product .info {
    padding: 0 6.7% 0 5%;
    margin: 3.3% 0 3.6%;
    box-sizing: border-box;
    position: relative;
    width: 59.375%; }
    @media (max-width: 600px) {
      .Product .info {
        width: 100%;
        margin: 25px 0 31px;
        padding: 0 19px 0 23px; } }
  .Product .info-title-price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap; }
    .Product .info-title-price .price {
      font-size: 21px;
      letter-spacing: -0.27px;
      margin-bottom: 20px; }
      @media (max-width: 900px) {
        .Product .info-title-price .price {
          margin-bottom: 1.1vh; } }
      @media (max-width: 600px) {
        .Product .info-title-price .price {
          font-size: 16px;
          margin-bottom: 9px; } }
  .Product .inventory {
    flex-basis: 100%;
    display: block;
    font-size: 13px;
    letter-spacing: 0;
    color: #939393;
    text-transform: uppercase; }
    @media (max-width: 900px) {
      .Product .inventory {
        font-size: 12px; } }
  .Product .add-to-cart {
    position: absolute;
    bottom: 0; }
    @media (max-width: 600px) {
      .Product .add-to-cart {
        position: relative;
        bottom: auto;
        margin-top: 25px; } }

.Cart .Product h2 {
  font-size: 28px;
  border: none;
  padding: 0; }

@media (max-width: 600px) {
  .Cart .Product .info-title-price {
    flex-direction: column;
    align-items: flex-start; } }

.Cart .Product .price {
  font-size: 18px; }
