/* BREAKPOINT SETTINGS */
/* BREAKPOINT MIXINS */
body {
  margin: 6.25%;
  font-family: 'Arial', sans-serif;
  background-color: #f4f5f7; }

h1 {
  font-size: 51px;
  font-weight: normal;
  color: #000000;
  margin: 0; }
  @media (max-width: 900px) {
    h1 {
      font-size: 38px; } }
  @media (max-width: 600px) {
    h1 {
      font-size: 28px; } }

h2 {
  font-size: 38px;
  font-weight: normal;
  letter-spacing: -0.41px; }
  @media (max-width: 900px) {
    h2 {
      font-size: 28px; } }
  @media (max-width: 600px) {
    h2 {
      font-size: 21px; } }

.store-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 21px; }
  @media (max-width: 600px) {
    .store-header {
      flex-direction: column;
      align-items: flex-start; } }
  .store-header h1 {
    margin-bottom: 10px; }

.hidden {
  display: none; }
