/* BREAKPOINT SETTINGS */
/* BREAKPOINT MIXINS */
.CartControl {
  display: flex;
  flex-direction: row; }
  .CartControl label {
    flex: 1;
    width: 100%;
    display: flex;
    position: relative; }
  .CartControl input {
    text-align: center;
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;
    border-left: 0;
    border-right: 0;
    font-size: 14px;
    letter-spacing: -0.59px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    position: absolute; }
  .CartControl .decrease-icon,
  .CartControl .increase-icon {
    width: 14px;
    height: 14px;
    display: block;
    position: relative; }
    .CartControl .decrease-icon:before,
    .CartControl .increase-icon:before {
      content: '';
      width: 100%;
      height: 2px;
      background-color: #8e939c;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto; }
  .CartControl .increase-icon:after {
    content: '';
    width: 100%;
    height: 2px;
    background-color: #8e939c;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    transform: rotate(90deg); }
