/* BREAKPOINT SETTINGS */
/* BREAKPOINT MIXINS */
.CartContainer > .Button {
  font-size: 14px; }

.CartContainer .icon-shopping-cart {
  margin-right: 8px; }

.close-cart {
  width: 30px;
  height: 30px;
  position: relative; }
  .close-cart:hover:before, .close-cart:hover:after {
    background-color: #000000; }
  .close-cart:before, .close-cart:after {
    content: '';
    width: 100%;
    height: 1px;
    background-color: #cbcbcb;
    position: absolute;
    left: 0;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg); }
  .close-cart:after {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg); }
