/* BREAKPOINT SETTINGS */
/* BREAKPOINT MIXINS */
#modal {
  background-color: #d5d6da;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center; }
  #modal:empty {
    display: none; }
  #modal .close-cart {
    position: absolute;
    top: 30px;
    right: 28px;
    z-index: 1; }
  #modal > div {
    background-color: white;
    width: 61%;
    height: 80%;
    border-radius: 15px;
    position: relative; }
    @media (max-width: 900px) {
      #modal > div {
        height: 100%;
        width: 100%;
        border-radius: 0; } }
  #modal .focus-trap {
    height: 100%; }
